<template>
  <div
    class="container-wrap"
    :style="style"
    @mousedown.left="control"
    @mouseup.left="release"
    @mouseleave="release"
  >
    <div class="blur-layer" >
      <div class="color-layer" :style="colorLayerStyle">
        <div class="noise-layer"></div>
      </div>
    </div>
    <div class="content" :style="`color:${darkMode?'white':'black'}`">
      <slot></slot>
    </div>
  </div>
</template>
<script lang="ts">
import {
  defineComponent,
  SetupContext,
  computed,
  onMounted,
  toRef
} from 'vue'
import { addCallBack } from '@/callbackPoll'
import { sharedState } from '../store'
import { num2color } from '@/util'
import { useWindowWrapStyle, useMouseMoveControl, useInitState, useControl } from './window'
type IContext = {} & SetupContext
type IProps = {
  initPos: {
    top: number;
    left: number;
  };
}
export default defineComponent({
  props: {
    initPos: {
      type: Object,
      default: () => ({
        top: 256,
        left: 256
      })
    }
  },
  setup (props) {
    const p = props as IProps
    const { initPos } = p
    const state = useInitState(initPos)
    const onMouseMove = useMouseMoveControl(state)
    const control = useControl(state)
    const release = (e: MouseEvent) => {
      const { type } = e
      switch (type) {
        case 'mouseleave':
          !state.flagSet.has('resize') && state.flagSet.clear()
          break
        case 'mouseup':
          state.flagSet.clear()
          break
      }
    }
    const { style } = useWindowWrapStyle(state)
    onMounted(() => {
      addCallBack('mousemove', onMouseMove)
    })
    const colorLayerStyle = computed(() => {
      return `background: rgba(${num2color(
        sharedState.darkMode ? 0 : 0xffffff
      )}, 0.4)`
    })
    return {
      style,
      colorLayerStyle,
      release,
      control,
      darkMode: toRef(sharedState, 'darkMode')
    }
  }
})
</script>
<style lang="scss" scoped>
.container-wrap {
  user-select: none;
  position: fixed;
  background: inherit;
  overflow: hidden;
  border: 2px solid white;
  .blur-layer {
    position: relative;
    top: -16px;
    left: -16px;
    width: calc(100% + 32px);
    height: calc(100% + 32px);
    background: inherit;
    filter: blur(12px);
    * {
      width: 100%;
      height: 100%;
    }
    .color-layer {
      background: rgba(255, 255, 255, 0.6);
    }
  }

  .content {
    padding: 16px;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    word-break: normal;
    z-index: 3;
    box-sizing: border-box;
  }
}
</style>
